import React, { useState, useRef, useEffect, useMemo } from "react";
import "./styles.scss";
import { hexToRgb, rgbToHex } from "../../helpers/color.js";

/**
 * TODO: —Å–æ—Ö—Ä–∞–Ω—è—Ç—å –ø–æ–ª–æ–∂–µ–Ω–∏–µ –ø–æ–ª–∑—É–Ω–∫–æ–≤
 * –¥–µ–ª–∞—Ç—å –≤ –≥–ª–æ–±–∞–ª —Å–æ—Å—Ç–∞—è–Ω–∏–∏ –∏–ª–∏ –≤—ã—Å—á–∏—Ç—ã–≤–≤–∞—Ç—å –ø–æ props.hexColor
 */
const ColorPalette = (props) => {
	const { hexColor, setHexColor } = props;

	const circle = useRef(null);
	const level = useRef(null);
	const [color, setColor] = useState(hexToRgb(hexColor));
	const [draging, setDraging] = useState(0);
	const [rangeColor, setRangeColor] = useState(() => {
		if (localStorage["rangeColor"]) return localStorage["rangeColor"];
		return 500;
	});
	const [rangeOpacity, setRangeOpacity] = useState(() => {
		if (localStorage["rangeOpacity"]) return localStorage["rangeOpacity"];
		return 255;
	});

	// FIXME: –∫–æ—Å—Ç—ã–ª—å?
	useEffect(() => {
		localStorage["rangeColor"] = rangeColor;
		localStorage["rangeOpacity"] = rangeOpacity;
	}, [rangeColor, rangeOpacity]);

	const result = useMemo(() => {
		if (rangeOpacity >= 255)
			return color.map((c) => Math.max(c, rangeOpacity - 255));
		else return color.map((c) => Math.min(c, rangeOpacity));
	}, [rangeOpacity, color]);

	useEffect(() => {
		const ss = rgbToHex(...result);
		setHexColor(ss);
	}, [result, setHexColor]);

	const colorStyle = useMemo(() => {
		let _c = 0; // color
		let _op = 0; // rangeOpacity
		if (rangeOpacity >= 255) {
			_c = 255;
			_op = (rangeOpacity - 255) / 255;
		} else {
			_c = 0;
			_op = (255 - rangeOpacity) / 255;
		}
		return {
			backgroundColor: `rgba(${_c},${_c},${_c},${_op})`,
		};
	}, [rangeOpacity]);

	const calcColor = () => {
		const line = Math.floor((1020 + 255 * 2) * (rangeColor / 1000));
		let _c = { r: 0, g: 0, b: 0 };

		if (line < 255) {
			_c.r = 255;
			_c.g = line % 255;
		} else if (line < 255 * 2) {
			_c.r = 255 - (line % 255);
			_c.g = 255;
		} else if (line < 255 * 3) {
			_c.g = 255;
			_c.b = line % 255;
		} else if (line < 255 * 4) {
			_c.g = 255 - (line % 255);
			_c.b = 255;
		} else if (line < 255 * 5) {
			_c.r = line % 255;
			_c.b = 255;
		} else if (line < 255 * 6) {
			_c.r = 255;
			_c.b = 255 - (line % 255);
		}
		setColor([_c.r, _c.g, _c.b]);
	};

	useEffect(calcColor, [rangeColor]);

	const rotationHandler = (event) => {
		if (!draging) return;

		let p_x = event.pageX;
		// let p_y = event.pageY;

		if (event.touches && event.touches[0]) {
			p_x = event.touches[0].pageX;
			// p_y = event.touches[0].pageY;
		}

		let el;
		if (draging === 2) el = level.current.getBoundingClientRect();
		else el = circle.current.getBoundingClientRect();

		let x2 = p_x - el.x;

		const endX = el.width;
		if (x2 < 0) x2 = 0;
		if (x2 >= endX) x2 = endX - 1;

		if (draging === 2) {
			const a = (x2 / endX) * 510;
			setRangeOpacity(Math.floor(a));
		} else {
			const a = (x2 / endX) * 1000;
			setRangeColor(Math.floor(a));
		}

		event.stopPropagation();
		event.preventDefault();
	};

	return (
		<div className="palette1">
			<div className="palette">
				<div
					className="palette-line-color"
					ref={circle}
					onMouseDown={() => setDraging(1)}
					onTouchStart={() => setDraging(1)}
					onMouseMove={rotationHandler}
					onTouchMove={rotationHandler}
					onMouseUp={() => setDraging(0)}
					onTouchCancel={() => setDraging(0)}
					onTouchEnd={() => setDraging(0)}
				>
					<div className="before" style={colorStyle}></div>
					<div
						className="palette-pointer"
						style={{
							left: `${Math.min(rangeColor / 10, 100)}%`,
							backgroundColor: `rgb(${result[0]}, ${result[1]}, ${result[2]})`,
						}}
					/>
				</div>
			</div>

			<div
				className="palette-line-level"
				ref={level}
				onMouseDown={() => setDraging(2)}
				onTouchStart={() => setDraging(2)}
				onMouseMove={rotationHandler}
				onTouchMove={rotationHandler}
				onMouseUp={() => setDraging(0)}
				onTouchCancel={() => setDraging(0)}
				onTouchEnd={() => setDraging(0)}
				style={{
					backgroundColor: `rgb(${color[0]}, ${color[1]}, ${color[2]})`,
				}}
			>
				<div
					className="palette-pointer"
					style={{
						left: `${Math.min((rangeOpacity / 510) * 100, 100)}%`,
						backgroundColor: `rgb(${result[0]}, ${result[1]}, ${result[2]})`,
					}}
				/>
			</div>
		</div>
	);
};

export default ColorPalette;
