import React, { useState, useEffect } from "react";
import fetchTimeout from "../../helpers/fetchWithTimeout";
import PopoverMenu from "../PopoverMenu/PopoverMenu";
import Toggle from "../BaseUI/Toggle";

const PageDevices = ({
	activeDevice,
	devices,
	addDevice,
	deleteDevice,
	setActiveDevice,
}) => {
	const [online, setOnline] = useState(() => devices.map(() => [2]));
	const setOnlineForI = (i, val) =>
		setOnline((oldVal) => {
			let newState = [...oldVal];
			newState[i] = val;
			return newState;
		});

	const [inputVal, setInputVal] = useState("191.168.1.69");
	const addDeviceFromInput = () => {
		addDevice({
			type: "ws_led_strip",
			name: "lol kek",
			_id: "test-" + Date.now(),
			url: inputVal,
		});
	};

	const maxAddress = 255;
	const [status, setStatus] = useState(0); // 0 –Ω–∏—á–µ, 1 –∏–¥–µ—Ç –ø–æ–∏—Å–∫
	const [progress, setProgress] = useState(0); // 0 –Ω–∏—á–µ, 1 –∏–¥–µ—Ç –ø–æ–∏—Å–∫
	const progressPluse = () => setProgress((oldVal) => oldVal + 1);

	const startSearch = () => {
		setStatus(1);
		setProgress(1);
	};

	const stopSearch = () => {
		setStatus(0);
		setProgress(0);
	};

	useEffect(() => {
		if (progress !== 0) setTimeout(_worker, 250);
	}, [progress]);

	const _worker = async (n) => {
		const url = `192.168.1.${progress}`;
		const urlFull = `http://${url}/are_you_here`;
		console.log(">>>>>>>>>> > > >", url);
		try {
			const res = await fetchTimeout(urlFull, { cache: "no-cache" }, 1000);
			if (res.ok) {
				const _json = await res.json();

				addDevice(
					Object.assign(_json, {
						url,
						isOnline: false,
					})
				);
			}
			// console.info(url, _json);
		} catch (err) {
			// console.log(url, "üí•");
		}
		if (status === 1 && progress < 10) {
			progressPluse();
		} else {
			stopSearch();
		}
	};

	const [showPopoverMenu, setShowPopoverMenu] = useState(false);
	const popoverProps = {
		isShow: showPopoverMenu,
		hide: () => setShowPopoverMenu(false),
		items: [
			{
				text: "–£–¥–∞–ª–∏—Ç—å",
				cb: () => {
					deleteDevice(holdTarget);
				},
			},
		],
	};
	const [holdTimeout, setHoldTimeout] = useState();
	const [holdTarget, setHoldTarget] = useState();
	const holdStart = (e) => {
		setHoldTarget(e.target.dataset.id);
		const ss = setTimeout(() => {
			setShowPopoverMenu((oldVal) => oldVal === false);
		}, 500);
		setHoldTimeout(ss);
		e.preventDefault();
	};
	const holdStop = (e) => clearTimeout(holdTimeout);

	useEffect(() => {
		const loop = async (i = 0) => {
			if (!devices[i]) return;
			const dev = devices[i];
			const url = `http://${dev.url}/are_you_here`;
			const params = { cache: "no-cache" };
			try {
				const res = await fetchTimeout(url, params, 500);
				if (res.ok) setOnlineForI(i, 1);
				else throw "e";
			} catch (error) {
				setOnlineForI(i, 0);
			}
			loop(++i);
		};
		loop();
	}, [devices]);

	return (
		<div>
			<div className="devices">
				{devices.map((d, i) => (
					<div
						className={`devices-btn d-flex align-items-center ${
							activeDevice === i ? "active" : ""
						} status-${online[i]}`}
						data-id={d._id}
						onMouseDown={holdStart}
						onMouseUp={holdStop}
						onTouchStart={(e) => {
							setActiveDevice(i);
							holdStart(e);
						}}
						onTouchEnd={holdStop}
						onClick={() => setActiveDevice(i)}
					>
						<div className="py-1 mr-auto">
							<h6>{d.name}</h6>
							<small className="text-secondary">
								{d.url}
								<div className="device-status" />
							</small>
						</div>
						<Toggle checked={true} />
					</div>
				))}
			</div>
			<div className="mt-4">
				{status === 0 && (
					<button className="btn btn-secondary w-100" onClick={startSearch}>
						–ü–æ–∏—Å–∫ —É—Å—Ç—Ä–æ–π—Å—Ç–≤
					</button>
				)}
				{status === 1 && (
					<>
						<button className="btn btn-danger w-100" onClick={stopSearch}>
							–û—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –ø–æ–∏—Å–∫
						</button>
						<p className="text-secondary">
							–ü—Ä–æ–≤–µ—Ä–µ–Ω–æ {progress} –∏–∑ {maxAddress}
						</p>
					</>
				)}
			</div>

			<div className="my-5">
				<div className="form-group mb-1">
					<label>–ê–¥—Ä–µ—Å—Å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞</label>
					<div className="input-group">
						<input
							type="text"
							className="form-control"
							value={inputVal}
							onChange={({ target }) => setInputVal(target.value)}
							placeholder="192.168.1.1"
						/>
						<div className="input-group-append">
							<button className="btn btn-warning" onClick={addDeviceFromInput}>
								<i class="fas fa-plus    "></i>
							</button>
						</div>
					</div>
				</div>

				<p className="text-center">
					<small>–†—É—á–Ω–æ–µ –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤</small>
				</p>
			</div>

			<PopoverMenu {...popoverProps} />
		</div>
	);
};

export default PageDevices;
