import React from "react";
import config from "../app.config";

const Navbar = (props) => {
	const items = config.sections;

	return (
		<div className="app-menu text-center">
			<div className="row">
				{items.map((item) => (
					<div
						className={`col-3 app-menu-btn ${
							props.page === item.n && "active"
						}`}
						onClick={() => props.setPage(item.n)}
						key={item.n}
					>
						<i className={"fas " + item.i} />
						<div>{item.ts}</div>
					</div>
				))}
			</div>
		</div>
	);
};

export default Navbar;
