import React, { useState, useEffect } from "react";
import ReconnectingWebSocket from "reconnecting-websocket";

import "./App.css";
import Navbar from "./compontns/Navbar";
import AppHeader from "./compontns/AppHeader";
import PageOptions from "./compontns/pages/PageOptions";
import PageEffects from "./compontns/pages/PageEffects";
import PageColors from "./compontns/pages/PageColors";
import PageDevices from "./compontns/pages/PageDevices";
import { useMemo } from "react";

const ws_opts = { maxEnqueuedMessages: 0 };
// let socket = new ReconnectingWebSocket("ws://192.168.1.3:81", [], ws_opts);
let socket = new ReconnectingWebSocket("ws://192.168.1.3:81", [], ws_opts);
console.log("sadasdasd");

setInterval(() => {
	// console.log(socket.readyState);
	if (socket.readyState !== 1) return;
	socket.send("p|" + Date.now());
}, 1000);

let ping_arr = [];

let lastUpdate = Date.now();

/**
 * TODO:
 * —Å–¥–µ–ª–∞—Ç—å –æ–±—Ä–∞–±–æ—Ç–∫—É –æ—à–∏–±–æ–∫ –≤ —Å–æ–∫–µ—Ç–∞—Ö, —á—Ç–æ–±—ã –ª–æ–≤–∏—Ç—å –∫–æ–≥–¥–∞ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —Ç–µ—Ä—è–µ—Ç —Å–≤—è–∑—å
 * https://medium.com/nuances-of-programming/%D0%B8%D1%81%D0%BF%D0%BE%D0%BB%D1%8C%D0%B7%D0%BE%D0%B2%D0%B0%D0%BD%D0%B8%D0%B5-%D0%B2%D0%B5%D0%B1%D1%81%D0%BE%D0%BA%D0%B5%D1%82%D0%BE%D0%B2-%D0%B2-%D0%BF%D1%80%D0%BE%D0%BC%D0%B8%D1%81-%D1%81%D1%82%D0%B8%D0%BB%D0%B5-81055345ef7b
 */

function App() {
	// let firstOpen = true;
	/**
	 * ==========
	 * –í–°–Ø –•–ï–†–ù–Ø –î–õ–Ø –î–ï–í–ê–ô–°–û–í
	 * ==========
	 */
	const [devices, setDevices] = useState(() => {
		if (localStorage["devices"]) return JSON.parse(localStorage["devices"]);
		return [];
	});
	const addDevice = (data) =>
		setDevices((oldState) => {
			const alreadyHave = oldState.filter((arr) => arr._id === data._id);
			if (alreadyHave.length === 0) return [...oldState, data];
			return oldState;
		});
	const deleteDevice = (devId) => {
		setDevices((oldVal) => oldVal.filter((arr) => arr._id !== devId));
	};
	useEffect(() => {
		localStorage["devices"] = JSON.stringify(devices);
	}, [devices]);

	const [activeDevice, setActiveDevice] = useState(0);
	const device = useMemo(() => {
		if (!devices[activeDevice]) setActiveDevice(0);
		return devices[activeDevice];
	}, [devices, activeDevice]);

	// TODO: —É—Å—Ç–∞–Ω–æ–≤–∫–∞ –Ω–∞—Å—Ç—Ä–æ–µ–∫ –¥–ª—è –≤—Å–µ—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤ –æ—Ç–¥–µ–ª—å–Ω–æ
	useEffect(() => {
		const dev = devices[activeDevice];
		setUrl(dev.url);

		socket.close();
		socket = new ReconnectingWebSocket("ws://" + dev.url + ":81", [], ws_opts);
	}, [activeDevice]);

	/**
	 * ==========
	 * –í–°–Ø –•–ï–†–ù–Ø –î–õ–Ø –¶–í–ï–¢–û–í
	 * ==========
	 */

	const [status, setStatus] = useState("offline");
	const [ping, setPing] = useState(0);
	const [page, setPage] = useState("devices");

	// COLORS
	const [hexColor, setHexColor] = useState("000000");
	const [gallery, setGalery] = useState([]);
	const colorPageProps = { hexColor, setHexColor, gallery, setGalery };

	// COLORS  ->  effects
	useEffect(() => {
		let data = ["000000", "000000", "000000", "000000", "000000", "000000"];
		data = data.map((d, i) => {
			if (gallery[i]) d = gallery[i];
			return d;
		});
		console.log(`g|${data.join("")}`);
		socket.send(`g|${data.join("")}`);
	}, [gallery]);

	const [effect, setEffect] = useState(0);
	const [speed, setSpeed] = useState(50);
	const [power, setPower] = useState(50);
	const [count, setCount] = useState(1);
	const effectOptsProps = {
		effect,
		setEffect,
		speed,
		setSpeed,
		power,
		setPower,
		count,
		setCount,
	};
	useEffect(() => socket.send(`1|${power}`), [power]);
	useEffect(() => socket.send(`2|${speed}`), [speed]);
	useEffect(() => socket.send(`3|${count}`), [count]);

	useEffect(() => {
		const now = Date.now();
		if (now - lastUpdate <= 1000 / 60) return;
		lastUpdate = now;
		socket.send(`c|${hexColor}`);
	}, [hexColor]);

	useEffect(() => {
		console.log(`e|${effect}`);
		socket.send(`e|${effect}`);
	}, [effect]);

	// –Ω–∞—Å—Ç—Ä–æ–π–∫–∏
	const [url, setUrl] = useState("http://192.168.1.3");
	const [brightness, setBrightness] = useState(50);

	socket.onmessage = function (event) {
		// console.log("–ü–æ–ª—É—á–µ–Ω—ã –¥–∞–Ω–Ω—ã–µ " + event.data);
		const [type, data] = event.data.split("|");

		if (type === "hi") {
			const _data = data.split("///");
			setBrightness(_data[1]);
			console.log(data);
		}

		if (event.data.indexOf("p|") !== -1) {
			const _ping = Date.now() - event.data.split("|")[1];

			if (ping_arr.length < 5) ping_arr.push(_ping);
			else ping_arr = [...ping_arr.splice(1, 4), _ping];

			const middle_value =
				ping_arr.reduce((pv, cv) => pv + cv) / ping_arr.length;

			setPing(Math.floor(middle_value));
		}
	};

	socket.onopen = function () {
		console.log("–°–æ–µ–¥–∏–Ω–µ–Ω–∏–µ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–æ.");
		setStatus("online");
	};
	socket.onclose = function (event) {
		if (event.wasClean) {
			console.log("–°–æ–µ–¥–∏–Ω–µ–Ω–∏–µ –∑–∞–∫—Ä—ã—Ç–æ —á–∏—Å—Ç–æ");
		} else {
			console.log("–û–±—Ä—ã–≤ —Å–æ–µ–¥–∏–Ω–µ–Ω–∏—è"); // –Ω–∞–ø—Ä–∏–º–µ—Ä, "—É–±–∏—Ç" –ø—Ä–æ—Ü–µ—Å—Å —Å–µ—Ä–≤–µ—Ä–∞
		}
		console.log("–ö–æ–¥: " + event.code + " –ø—Ä–∏—á–∏–Ω–∞: " + event.reason);

		setStatus("offline");
	};
	socket.onerror = function (error) {
		console.log("–û—à–∏–±–∫–∞ " + error.message);
		setStatus("offline");
		// socket.close();
	};

	useEffect(() => {
		console.log("üèì", `b|${brightness}`);
		socket.send(`b|${brightness}`);
	}, [brightness]);

	const e_sendName = (value = `device_${Date.now()}`) => {
		console.log("üèì", "N|" + value);
		socket.send("N|" + value);
	};
	const e_saveToEEPROM = () => {
		console.log("üèì", "e_saveToEEPROM()");
		// socket.send("B|123");
	};

	// useEffect(()=>{
	// 	if(page === 'devices') {

	// 	}
	// }, [page])

	return (
		<div className="container">
			<AppHeader {...{ page, device, ping, status }} />
			<Navbar {...{ setPage, page }} />
			{page === "devices" && (
				<PageDevices
					{...{
						activeDevice,
						devices,
						addDevice,
						deleteDevice,
						setActiveDevice,
					}}
				/>
			)}
			{page === "colors" && <PageColors {...colorPageProps} />}
			{page === "effects" && <PageEffects {...effectOptsProps} />}
			{page === "options" && (
				<PageOptions
					{...{
						device,
						url,
						setUrl,
						brightness,
						setBrightness,
						e_sendName,
						e_saveToEEPROM,
					}}
				/>
			)}
		</div>
	);
}

export default App;
