import React, { useState } from "react";

import Slider from "react-rangeslider";
// import "react-rangeslider/lib/index.css";

const PageEffects = (props) => {
	const { effect, setEffect } = props;
	const { speed, setSpeed, power, setPower, count, setCount } = props;

	const [optIsShow, setOptIsShow] = useState(false);
	const optIsShowHandler = () => setOptIsShow((oldVal) => oldVal !== true);

	let n = 0;
	let _effects = [
		{ n: [n++], l: "–í—ã–∫–ª—é—á–µ–Ω–æ" },
		{ n: [n++, n++, n++, n++], l: "–†–∞–¥—É–≥–∞", i: "üåà" },
		{ n: [n++, n++], l: "–ù–æ–≤–æ–≥–æ–¥–Ω—è—è –≥–∏—Ä–ª—è–Ω–¥–∞", i: "üéÑ" },
		{ n: [n++], l: "–°—Ç—Ä–∞–∑—ã", i: "ü§©" },
		{ n: [n++, n++, n++], l: "–í–æ–ª–Ω—ã", i: "üåä" },
		{ n: [n++, n++], l: "–ü–ª–∞–º—è", i: "üî•" },
		{ n: [0], l: "–ü–ª–∞–≤–Ω–∞—è —Å–º–µ–Ω–∞ —Ü–≤–µ—Ç–æ–≤", i: "üé®" },
		{ n: [0], l: "–ü–æ–ª–∏—Ü–∏—è", i: "üëÆ‚Äç‚ôÄÔ∏è" },
		{ n: [0], l: "–§–µ–µ—Ä–≤–µ—Ä–∫", i: "üí•" },
		{ n: [0], l: "–ö–æ–Ω—Ñ–µ—Ç–∞", i: "üç≠" },
		{ n: [0], l: "–ë–µ–≥–∞—é—â–∏–π –æ–≥–æ–Ω–µ–∫", i: "‚ú®" },
		{ n: [0], l: "–°–≤–µ—Ç–ª—è—á–∫–∏", i: "üí°" },
	];
	const [effects, setEffects] = useState(
		_effects.map((a) => {
			a.active = 0;
			if (a.n.indexOf(effect) !== -1) a.active = a.n.indexOf(effect);
			return a;
		})
	);
	const [activeI, setActiveI] = useState(() => {
		let answer = 0;
		_effects.forEach((arr, i) => {
			if (arr.n.indexOf(effect) !== -1) answer = i;
		});
		return answer;
	});
	const addActiveNum = (i) => {
		const newState = [...effects];
		if (activeI === i && newState[i].n.length > 1) {
			newState[i].active += 1;
			if (newState[i].active >= effects[i].n.length) newState[i].active = 0;
		}
		setEffects(newState);
	};

	const effectClick = (i) => {
		addActiveNum(i);
		setActiveI(i);
		const answer = effects[i].n[effects[i].active];
		setEffect(answer);
	};

	return (
		<div className="row px-2 ">
			{effects.map((ef, i) => (
				<div className="col-6 col-sm-4 px-1" key={ef.l}>
					<div
						className={`effect-btn d-flex align-items-center ${
							activeI === i && " active"
						}`}
						onClick={() => effectClick(i)}
					>
						<span>{ef.l}</span>
						{ef.i && <span className="icon">{ef.i}</span>}

						{ef.n.length > 1 && (
							<div className="effect-types">
								{ef.n.map((m, i) => (
									<span className={ef.active === i && "active"} />
								))}
							</div>
						)}
					</div>
				</div>
			))}

			<div className="effect-settings-btn" onClick={optIsShowHandler}>
				<i class="fas fa-cog"></i>
				{/* <i class="fas fa-angle-double-down"></i> */}
			</div>
			{optIsShow === true && (
				<div className="effect-settings">
					<div class="form-group">
						<div className="d-flex effect-settings-label">
							<div className="mr-auto">–°–∫–æ—Ä–æ—Å—Ç—å</div>
							<span className="badge badge-secondary">{speed + 50}%</span>
						</div>
						<Slider
							min={1}
							max={100}
							value={speed}
							onChange={(v) => setSpeed(v)}
							tooltip={false}
						/>
					</div>
					<div class="form-group">
						<div className="d-flex effect-settings-label">
							<div className="mr-auto">–ò–Ω—Ç–µ–Ω—Å–∏–≤–Ω–æ—Å—Ç—å</div>
							<span className="badge badge-secondary">{power}</span>
						</div>
						<Slider
							value={power}
							onChange={(v) => setPower(v)}
							tooltip={false}
						/>
					</div>
					<div class="form-group">
						<div className="d-flex effect-settings-label">
							<div className="mr-auto">–ö–æ–ª-–≤–æ</div>
							<span className="badge badge-secondary">{count}</span>
						</div>
						<Slider
							value={count}
							onChange={(v) => setCount(v)}
							tooltip={false}
						/>
					</div>
				</div>
			)}
		</div>
	);
};

export default PageEffects;
